.Button {
    padding: 10px 18px;
    border-radius: 5px;
    background-color: #4252ab;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    text-align: center;
    display: inline-block;
    color: #fff;
    border: 0;
    text-decoration: none;
    cursor: pointer;
    font-family: inherit;
    font-size: 16px;
    line-height: 1.5;
    font-style: normal;
    font-weight: 500;
    max-width: 200px;
    margin: 0 auto;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  }
  
  .Button:hover,
  .Button:focus {
    background-color: #4252ab;
  }